<template>
  <div class="home-container">
    <loading :loading="loading" />
    <div class="home-top">
      <el-image
        class="home-bg"
        style="position: absolute"
        :src="require('../../assets/home_bg.gif')"
        fit="cover"
      ></el-image>
      <div class="navigation bcolor">
        <div class="navigation-icon">
          <div>Cosmetics<span class="sbgcolor"> Volunteer</span></div>
          
        </div>
        <div class="navigation-options">
          <!-- <div class="navigation-option"></div> -->
          <div
            class="navigation-option"
            @click="goLogin"
            v-if="currentUser == null"
          >
            /
          </div>
          <div
            class="navigation-option"
            v-if="currentUser != null"
            @click="updatePerson"
          >
            {{
              currentUser.role == 0
                ? ""
                : currentUser.role == 1
                ? ""
                : currentUser.role == 2
                ? ""
                : ""
            }}
          </div>

          <div
            class="navigation-option"
            @click="goLogin"
            v-if="currentUser != null"
          >
            
          </div>
        </div>
      </div>
      <div class="home-title">Cosmetics Volunteer</div>
    </div>

    <div class="home-main">
      <div class="home-main-left hidden-md-and-down">
        <!-- <div class="main-left-title fcolor">
          
          <div class="main-left-title-us">
            <em>Search Something By keywords</em>
          </div>
        </div>
        <div class="search">
          <el-input></el-input>
          <el-button type="primary" style="margin-left: 10px"></el-button>
        </div> -->
        <div class="main-left-title fcolor">
          
          <div class="main-left-title-us">
            <em>Data Category </em>
          </div>
        </div>
        <div class="main-left-options">
          <div
            class="main-left-option"
            :class="currentShow == 1 ? 'main-left-option-choose' : 'fcolor'"
            @click="changeData(1)"
            v-if="currentUser != null && currentUser.role < 3"
          >
            
          </div>
          <div
            class="main-left-option"
            :class="currentShow == 2 ? 'main-left-option-choose' : 'fcolor'"
            @click="changeData(2)"
          >
            
          </div>
          <div
            class="main-left-option"
            :class="currentShow == 3 ? 'main-left-option-choose' : 'fcolor'"
            @click="changeData(3)"
            v-if="currentUser != null && currentUser.role < 3"
          >
            
          </div>
          <div
            class="main-left-option"
            :class="currentShow == 0 ? 'main-left-option-choose' : 'fcolor'"
            @click="changeData(0)"
            v-if="currentUser != null && currentUser.role == 0"
          >
            
          </div>
        </div>
        <div
          class="main-left-title fcolor"
          v-if="currentUser != null && currentUser.role < 2"
        >
          
          <div class="main-left-title-us">
            <em>Optional Operation</em>
          </div>
        </div>
        <div
          class="main-left-options"
          v-if="currentUser != null && currentUser.role < 2"
        >
          <div class="main-left-option fcolor" @click="addRecord"></div>
          <!-- <div class="main-left-option fcolor"></div>  -->
        </div>
      </div>

      <div class="home-main-right">
        <transition name="el-zoom-in-top">
          <div class="main-right-container" v-show="currentShow == 1">
            <el-empty
              description=""
              v-show="tests.length == 0"
            ></el-empty>
            <div
              class="test-card"
              v-for="(item, index) in tests"
              :key="item.id"
            >
              <el-descriptions
                class="margin-top"
                :title="'' + item.number + ''"
                :column="4"
                border
              >
                <template slot="extra">
                  <el-button size="small" @click="commitSolution(item)"
                    ></el-button
                  >
                  <el-button
                    size="small"
                    @click="updateTest(index)"
                    v-if="currentUser != null && currentUser.role < 2"
                    ></el-button
                  >
                  <!-- <el-button size="small" @click="deleteRecord('test',item.id)"></el-button> -->
                </template>
                <el-descriptions-item>
                  <template slot="label">  </template>
                  {{ item.number }}
                </el-descriptions-item>
                <el-descriptions-item span="2">
                  <template slot="label">  </template>
                  {{ item.name }}
                </el-descriptions-item>
                <el-descriptions-item>
                  <template slot="label">  </template>
                  {{ item.age }} 
                </el-descriptions-item>
                <el-descriptions-item>
                  <template slot="label" span="1">  </template>
                  {{ item.researcherId }}
                </el-descriptions-item>
                <el-descriptions-item>
                  <template slot="label">  </template>
                  {{ item.researcher }}
                </el-descriptions-item>
                <el-descriptions-item>
                  <template slot="label">  </template>
                  {{ item.testorId }}
                </el-descriptions-item>
                <el-descriptions-item>
                  <template slot="label">  </template>
                  {{ item.testor }}
                </el-descriptions-item>
                <el-descriptions-item>
                  <template slot="label">  </template>
                  {{ item.effect }}
                </el-descriptions-item>
              </el-descriptions>
            </div>
          </div>
        </transition>

        <!--  -->
        <transition name="el-zoom-in-top">
          <div class="main-right-container" v-show="currentShow == 2">
            <el-empty
              description=""
              v-show="solutions.length == 0"
            ></el-empty>
            <div
              class="article"
              v-for="item in solutions"
              :key="item.id"
              v-show="currentShow != 0"
            >
              <div class="article-right">
                <div class="article-title">
                   "{{ item.number }}" 
                  <div
                    class="article-status"
                    :class="
                      item.status == -1
                        ? 'article-status-fail'
                        : item.status == 0
                        ? 'article-status-wait'
                        : item.status == 1
                        ? 'article-status-pass'
                        : 'article-status-finish'
                    "
                  >
                    {{
                      item.status == -1
                        ? ""
                        : item.status == 0
                        ? ""
                        : item.status == 1
                        ? ""
                        : ""
                    }}
                  </div>
                </div>
                <div class="article-msg">
                   {{ item.testor }}   {{ item.researcher }} 
                  {{ item.createAt }}
                </div>
                <div class="article-content">{{ item.content }}</div>
                <div class="article-label"># </div>
                <div class="article-btn">
                  <div
                    class="article-detail fcolor"
                    :class="
                      articleMouseIdx == item.id && mouseOverType == 'detail'
                        ? 'scolor'
                        : ''
                    "
                    @click="goDetail(item.id, 'detail')"
                    @mouseover="articleMouseOver(item.id, 'detail')"
                    @mouseleave="articleMouseOver(-1)"
                  >
                    /{{
                      articleMouseIdx == item.id && mouseOverType == "detail"
                        ? " "
                        : ""
                    }}
                  </div>
                  <div
                    class="article-detail fcolor"
                    :class="
                      articleMouseIdx == item.id && mouseOverType == 'volunteer'
                        ? 'scolor'
                        : ''
                    "
                    @click="goDetail(item.id, 'volunteer')"
                    @mouseover="articleMouseOver(item.id, 'volunteer')"
                    @mouseleave="articleMouseOver(-1)"
                    v-show="item.status == 1"
                  >
                    {{
                      articleMouseIdx == item.id && mouseOverType == "volunteer"
                        ? " "
                        : ""
                    }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>

        <!--  -->
        <transition name="el-zoom-in-top">
          <div class="main-right-container" v-show="currentShow == 3">
            <el-empty
              description=""
              v-show="reportTotal == 0"
            ></el-empty>
            <div
              class="article"
              v-for="item in reports"
              :key="item.id"
              v-show="currentShow != 0"
            >
              <div class="article-right">
                <div class="article-title">
                   "{{ item.number }}" 
                </div>
                <div class="article-msg">
                   {{ item.testor }}   {{ item.researcher }} 
                  {{ item.createAt }}
                </div>
                <div class="article-content">{{ item.report }}</div>
                <div class="article-label"># </div>
                <div class="article-btn">
                  <div
                    class="article-detail fcolor"
                    :class="
                      articleMouseIdx == item.id && mouseOverType == 'report'
                        ? 'scolor'
                        : ''
                    "
                    @click="goDetail(item.id, 'report')"
                    @mouseover="articleMouseOver(item.id, 'report')"
                    @mouseleave="articleMouseOver(-1)"
                  >
                    {{
                      articleMouseIdx == item.id && mouseOverType == "report"
                        ? " "
                        : ""
                    }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </transition>

        <!--  -->
        <transition name="el-zoom-in-top">
          <div class="main-right-container" v-show="currentShow == 0">
            <el-table :data="users" style="width: 100%">
              <el-table-column type="expand">
                <template slot-scope="props">
                  <el-form
                    label-position="left"
                    inline
                    class="user-table-expand"
                  >
                    <el-form-item label="">
                      <span>{{ props.row.id }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.username }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.gender == 0 ? "" : "" }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{
                        props.row.role == 0
                          ? ""
                          : props.row.role == 1
                          ? ""
                          : props.row.role == 2
                          ? ""
                          : ""
                      }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.phone }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.email }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.updateAt }}</span>
                    </el-form-item>
                    <el-form-item label="">
                      <span>{{ props.row.createAt }}</span>
                    </el-form-item>
                    <el-form-item>
                      <el-button
                        style="width: 150px"
                        @click="updateUser(props.$index)"
                        ></el-button
                      >
                      <!-- <el-button
                        style="width: 150px"
                        @click="deleteRecord('user', props.row.id)"
                        ></el-button
                      > -->
                    </el-form-item>
                  </el-form>
                </template>
              </el-table-column>
              <el-table-column prop="createAt" label="" width="180">
              </el-table-column>
              <el-table-column prop="id" label="" width="180">
              </el-table-column>
              <el-table-column prop="username" label="" width="180">
              </el-table-column>
              <el-table-column prop="phone" label=""> </el-table-column>
            </el-table>
          </div>
        </transition>

        <el-pagination
          background
          layout="prev, pager, next"
          :total="
            currentShow == 0
              ? userTotal
              : currentShow == 1
              ? testTotal
              : currentShow == 2
              ? solutionTotal
              : reportTotal
          "
          v-show="
            (currentShow == 0 && userTotal != 0) ||
            (currentShow == 1 && testTotal != 0) ||
            (currentShow == 2 && solutionTotal != 0) ||
            (currentShow == 3 && reportTotal != 0)
          "
          style="margin-top: 30px"
          @current-change="changePage"
        >
        </el-pagination>
      </div>
    </div>

    <!-- / -->
    <el-dialog title="/" :visible.sync="userFormVisible">
      <el-form :model="userForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="userForm.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="userForm.password"
            autocomplete="off"
            show-password
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="userForm.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="userForm.email" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-radio v-model="userForm.role" label="0"></el-radio>
          <el-radio v-model="userForm.role" label="1"></el-radio>
          <el-radio v-model="userForm.role" label="2"></el-radio>
          <el-radio v-model="userForm.role" label="3"></el-radio>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="userFormVisible = false"> </el-button>
        <el-button type="primary" @click="saveRecord('user', userForm)"
          > </el-button
        >
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="solutionFormVisible">
      <el-form :model="solutionForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="solutionForm.number"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="solutionForm.researcherId"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="solutionForm.researcher"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="solutionForm.testorId"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="solutionForm.testor"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            type="textarea"
            :rows="10"
            v-model="solutionForm.content"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="solutionFormVisible = false"> </el-button>
        <el-button type="primary" @click="saveRecord('solution', solutionForm)"
          > </el-button
        >
      </div>
    </el-dialog>

    <!--  -->
    <el-dialog
      title=""
      :visible.sync="personFormVisible"
      v-if="currentUser != null"
    >
      <el-form :model="personForm">
        <el-form-item label="" :label-width="formLabelWidth">
          {{
            currentUser.role == 0
              ? ""
              : currentUser.role == 1
              ? ""
              : currentUser.role == 2
              ? ""
              : ""
          }}
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="personForm.username" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="personForm.password"
            autocomplete="off"
            show-password
          ></el-input>
        </el-form-item>

        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="personForm.phone" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="personForm.email" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="personFormVisible = false"> </el-button>
        <el-button type="primary" @click="changePerson"> </el-button>
      </div>
    </el-dialog>

    <div class="main-bottom bcolor">
      <em
        >2021-2022 Cosmetics <span class="scolor">ICP202110000</span></em
      >
      <div class="line"></div>
      <em>
        Power By
        <span class="scolor">Spring</span> & <span class="scolor">Vue</span> &
        <span class="scolor">ElementUI</span></em
      >
    </div>

        <!-- / -->
    <el-dialog title="/" :visible.sync="testFormVisible" @close="closeTestDialog">
      <el-form :model="testForm">
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="testForm.number" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="testForm.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="testForm.age" autocomplete="off"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="testForm.researcherId"
            autocomplete="off"
          ></el-input>
        </el-form-item> -->
        <el-form-item label="" :label-width="formLabelWidth" >
          <el-select v-model="testForm.researcherId" placeholder="" @change="changeResearcher">
              <el-option
                v-for="item in researchers"
                :key="item.id"
                :label="item.id"
                :value="item.id"
              >
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="researcherChoose!=null?researcherChoose.username:testForm.researcher"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>
        <!-- <el-form-item label="" :label-width="formLabelWidth">
          <el-input v-model="testForm.testorId" autocomplete="off"></el-input>
        </el-form-item> -->
        <el-form-item label="" :label-width="formLabelWidth">
          <el-select v-model="testForm.testorId" placeholder="" @change="changeTestor">
              <el-option
                v-for="item in testors"
                :key="item.id"
                :label="item.id"
                :value="item.id"
              >
              </el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            v-model="testorChoose!=null?testorChoose.username:testForm.testor"
            autocomplete="off"
            disabled
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth">
          <el-input
            type="textarea"
            :rows="3"
            v-model="testForm.effect"
            autocomplete="off"
          ></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="testFormVisible = false"> </el-button>
        <el-button type="primary" @click="saveRecord('test', testForm)"
          > </el-button
        >
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Home from "./Home.js";
export default Home;
</script>

<style scoped>
@import "./Home.css";
@import "element-ui/lib/theme-chalk/display.css";
</style>


